.skills {
  text-align: center;
  padding: 50px 20px;
  background: linear-gradient(135deg, #121212, #414040);
  color: white;
}

h2 {
  font-size: 2.3rem;
  margin-bottom: 20px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.skills-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 25px;
}

.skills-category {
  background: rgba(255, 255, 255, 0.223);
  padding: 25px;
  border-radius: 15px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
  position: relative;
  overflow: hidden;
}

.skills-category:hover {
  transform: scale(1.08);
  background: linear-gradient(135deg, #793d2e, #78553b);
  box-shadow: 0 0 15px rgba(165, 125, 114, 0.7);
}

h3 {
  font-size: 1.6rem;
  margin-bottom: 15px;
  position: relative;
}

.category-box {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
}

.skill-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.289);
  padding: 18px;
  border-radius: 12px;
  width: 130px;
  height: 130px;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  position: relative;
}

.skill-box:hover {
  transform: scale(1.12) rotate(3deg);
  background: linear-gradient(135deg, #bf6048, #946949);
  box-shadow: 0 0 12px rgba(219, 110, 82, 0.7);
}

.skill-box svg {
  font-size: 2.8rem;
  color: white;
  transition: transform 0.3s ease-in-out;
}

.skill-box:hover svg {
  transform: rotate(30deg) scale(1.2);
}

p {
  margin-top: 10px;
  font-size: 0.8rem;
  font-weight: bold;
  text-transform: uppercase;
}
