/* ContactMe.css */
.contact-section {
  text-align: center;
  padding: 60px 20px;
  background: linear-gradient(135deg, #2c3e50, #abdae1); /* Soft gradient */
  color: white;
  min-height: 100vh;
  position: relative;
}

.contact-container {
  max-width: 650px;
  margin: auto;
  position: relative;
}

.logo {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  margin-bottom: 20px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease-in-out;
}

.logo:hover {
  transform: scale(1.1);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.4);
}

/* Contact Title */
h2 {
  font-size: 3rem;
  text-transform: uppercase;
  font-weight: bold;
  color: white;
  position: relative;
  display: inline-block;
  padding-bottom: 10px;
}

/* Paper Plane Animation */
h2::after {
  content: "✈"; /* Paper plane emoji */
  font-size: 1.5rem;
  position: absolute;
  top: -10px;
  right: -30px;
  animation: flyAround 3s infinite linear;
}

/* Flying Paper Plane */
@keyframes flyAround {
  0% {
    transform: translate(0, 0) rotate(0deg);
  }
  25% {
    transform: translate(15px, -10px) rotate(15deg);
  }
  50% {
    transform: translate(30px, 0) rotate(30deg);
  }
  75% {
    transform: translate(15px, 10px) rotate(15deg);
  }
  100% {
    transform: translate(0, 0) rotate(0deg);
  }
}

p {
  font-size: 1.2rem;
  margin-bottom: 20px;
  color: #f0f0f0;
}

/* Social Links */
.social-links {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
}

.social-links a {
  text-decoration: none;
  font-size: 1.3rem;
  font-weight: bold;
  color: white;
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 12px 18px;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.1);
  transition: transform 0.3s ease-in-out, box-shadow 0.3s;
}

.social-links a:hover {
  transform: scale(1.1);
  box-shadow: 0 6px 15px rgba(255, 255, 255, 0.2);
}

/* Image Container */
.image-container {
  margin-top: 40px;
  display: flex;
  justify-content: center;
}

.fade-image {
  width: 220px;
  height: 220px;
  border-radius: 50%;
  object-fit: cover;
  transition: opacity 1s ease-in-out, transform 0.5s;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

.fade-image:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.4);
}

/* Responsive Design */
@media (max-width: 768px) {
  h2 {
    font-size: 2.5rem;
  }

  .social-links a {
    font-size: 1.1rem;
    padding: 10px 15px;
  }

  .fade-image {
    width: 180px;
    height: 180px;
  }
}
