.portfolio__filters {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  column-gap: 1.875rem;
  margin-bottom: 2.5rem;
}

.portfolio__item {
  cursor: pointer;
  font-weight: var(--font-bold);
}

.portfolio__item {
  cursor: pointer;
  font-weight: var(--font-bold);
  transition: 0.3s;
}

.portfolio__item-active {
  color: var(--first-color);
}

.portfolio__item:hover {
  color: var(--first-color);
}

.portfolio__container {
  grid-template-columns: repeat(3, 1fr);
  gap: 1.875rem;
}

.portfolio__card {
  position: relative;
  overflow: hidden;
  border-radius: var(--border-radius);
  box-shadow: var(--shadow);
}

.portfolio__mask {
  background-color: #50926c;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  transition: 0.3s;
  opacity: 0;
}

.portfolio__card:hover .portfolio__mask {
  opacity: 0.9;
}

.portfolio__category {
  color: #fff;
  background-color: var(--first-color);
  border-bottom-left-radius: 0.98rem;
  border-bottom-right-radius: 0.98rem;
  position: absolute;
  top: 0;
  left: 1.5rem;
  font-size: var(--small-font-size);
  display: inline-block;
  padding: 0.19rem 0.625rem;
  transform: translateY(-40px);
  transition: 0.3s;
}

.portfolio__title {
  color: #fff;
  font-size: var(--h3-font-size);
  margin: 0 0 0.98rem;
  padding: 0 1.25rem;
  position: absolute;
  top: 3.75rem;
  transform: translateY(30px);
  transition: 0.3s;
  opacity: 0;
}

.portfolio__button {
  color: #fff;
  position: absolute;
  bottom: 1.5rem;
  left: 1.5rem;
  font-size: var(--h3-font-size);
  display: block;
  background-color: #34825c;
  height: 40px;
  width: 40px;
  cursor: pointer;
  border-radius: 50%;
  text-align: center;
  line-height: 42px;
  transition: 0.3s;
  opacity: 0;
}

.portfolio__github-button {
  color: #fff;
  position: absolute;
  bottom: 1.5rem;
  left: 4.5rem;
  font-size: var(--h3-font-size);
  display: block;
  background-color: #19257d;
  height: 40px;
  width: 40px;
  cursor: pointer;
  border-radius: 50%;
  text-align: center;
  line-height: 42px;
  transition: 0.3s;
  opacity: 0;
}
.portfolio__card {
  position: relative;
  overflow: hidden;
  border-radius: var(--border-radius);
  box-shadow: var(--shadow);
  transition: transform 0.4s ease-in-out, border 0.4s ease-in-out;
  border: 2px solid transparent;
}

/* Zoom effect on hover */
.portfolio__card:hover {
  transform: scale(1.05);
  border-color: transparent;
  animation: borderAnimation 1.5s infinite linear;
}

/* Glowing border animation */
@keyframes borderAnimation {
  0% {
    border: 2px solid #ff7675;
    box-shadow: 0px 0px 10px #ff7675;
  }
  33% {
    border: 2px solid #fdcb6e;
    box-shadow: 0px 0px 10px #fdcb6e;
  }
  66% {
    border: 2px solid #74b9ff;
    box-shadow: 0px 0px 10px #74b9ff;
  }
  100% {
    border: 2px solid #ff7675;
    box-shadow: 0px 0px 10px #ff7675;
  }
}

/* Add transition to buttons */
.portfolio__button,
.portfolio__github-button {
  transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
}

/* Scale up buttons on hover */
.portfolio__card:hover .portfolio__button,
.portfolio__card:hover .portfolio__github-button {
  transform: scale(1.2);
}


.portfolio__card:hover .portfolio__button,
.portfolio__card:hover .portfolio__github-button {
  opacity: 1;
}

.portfolio__card:hover .portfolio__title,
.portfolio__card:hover .portfolio__category {
  opacity: 1;
  transform: translateY(0);
}

@media screen and (max-width: 1024px) {
  .portfolio__filters {
    justify-content: center;
  }

  .portfolio__container {
    grid-template-columns: repeat(2, 330px);
    justify-content: center;
    row-gap: 1.875rem;
  }
}

@media screen and (max-width: 768px) {
  .portfolio__container {
    grid-template-columns: 310px;
    justify-content: center;
    row-gap: 1.875rem;
  }

  .portfolio__filters {
    row-gap: 0.25rem;
  }
}

@media screen and (max-width: 350px) {
  .portfolio__container {
    grid-template-columns: 1fr;
  }
}
