:root {
  /* Light Mode Colors */
  --text-color: #333;
  --container-color: #ffffff;
  --first-color: #007bff;
  --second-color: #0056b3;
  --shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
  --border-radius: 10px;
  --bg-color: #7a7373;

}

.dark-mode {
  /* Dark Mode Colors */
  --bg-color: #121212;
  --text-color: #e0e0e0;
  --container-color: #1e1e1e;
  --first-color: #ff9800;
  --second-color: #e65100;
  --shadow: 0px 5px 15px rgba(255, 255, 255, 0.1);
}
body {
  background-color: var(--bg-color);
  color: var(--text-color);
  transition: background-color 0.3s, color 0.3s;
}


.blog__container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr)); /* Auto adjust */
  gap: 1.5rem 2rem; /* Row and column spacing */
  justify-content: center;
  background-color: var(--bg-color) !important;
  padding: 2rem;
}

.blog__card {
  box-shadow: var(--shadow);
  border-radius: var(--border-radius);
  overflow: hidden;
  transition: transform 0.3s ease-in-out, box-shadow 0.3s;
  background-color: var(--container-color);
}

.blog__card:hover {
  transform: translateY(-8px); /* Lift effect */
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
}

.blog__thumb {
  position: relative;
  overflow: hidden;
  border-top-left-radius: var(--border-radius);
  border-top-right-radius: var(--border-radius);
}

.blog__img {
  width: 100%;
  height: auto;
  display: block;
  transition: transform 0.3s ease-in-out;
}

.blog__thumb:hover .blog__img {
  transform: scale(1.1);
}

.blog__category {
  color: #fff;
  background-color: var(--first-color);
  border-bottom-left-radius: 0.9375rem;
  border-bottom-right-radius: 0.9375rem;
  position: absolute;
  top: 0;
  left: 1rem;
  font-size: var(--small-font-size);
  display: inline-block;
  padding: 0.3rem 0.8rem;
  font-weight: bold;
  transition: background-color 0.3s;
  z-index: 1;
}

.blog__thumb:hover .blog__category {
  background-color: var(--second-color);
}

.blog__details {
  background-color: var(--container-color);
  padding: 1.5rem;
  text-align: center;
  border-bottom-left-radius: var(--border-radius);
  border-bottom-right-radius: var(--border-radius);
}

.blog__title {
  font-size: var(--h3-font-size);
  margin-bottom: 0.5rem;
  transition: color 0.3s;
}

.blog__title a {
  text-decoration: none;
  color: var(--text-color);
}

.blog__title a:hover {
  color: var(--first-color);
}

.blog__meta {
  display: flex;
  justify-content: center;
  gap: 0.6rem;
  font-size: var(--small-font-size);
  color: #8b88b1;
}

.blog__dot {
  font-weight: var(--font-bold);
}

/* Add Extra Gap After 2nd Row */
.blog__container > :nth-child(2n) {
  margin-bottom: 2rem;
}

/* Responsive Design */
@media screen and (max-width: 1024px) {
  .blog__container {
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    gap: 2rem;
  }
}

@media screen and (max-width: 768px) {
  .blog__container {
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 1.5rem;
  }
}

@media screen and (max-width: 480px) {
  .blog__container {
    grid-template-columns: 1fr;
  }

  .blog__card {
    max-width: 100%;
  }
}

/* Light & Dark Mode Toggle */
.toggle-btn {
  position: fixed;
  top: 1rem;
  right: 1rem;
  background-color: var(--first-color);
  color: #fff;
  border: none;
  padding: 0.7rem 1rem;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: background 0.3s;
}

.toggle-btn:hover {
  background-color: var(--second-color);
}
