

/* Section Styles */
.sports {
    padding: 50px 20px;
    text-align: center;
    background: var(--background-color);
    transition: background 0.3s ease-in-out;
}

.sports__container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
}

.sports__card {
    width: 250px;
    padding: 15px;
    border-radius: 15px;
    background: var(--gradient);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s, box-shadow 0.3s;
    position: relative;
    overflow: hidden;
}

.sports__card:hover {
    transform: scale(1.08);
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.3);
}

.sports__circle {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    background: var(--hover-color);
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    box-shadow: 0 0 20px var(--sparkle-color);
    position: relative;
    transition: transform 0.3s ease-in-out;
}

.sports__circle:hover {
    transform: rotate(10deg) scale(1.1);
}

.sports__circle::before {
    content: '';
    width: 140px;
    height: 140px;
    border-radius: 50%;
    position: absolute;
    background: var(--sparkle-color);
    z-index: -1;
    opacity: 0.5;
}

.sports__img {
    width: 90px;
    height: 90px;
    border-radius: 50%;
    filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.2));
}

.sports__details {
    margin-top: 10px;
    color: var(--text-color);
    font-weight: bold;
    transition: color 0.3s;
}
.sports__title {
    font-size: 15px;
    color: var(--text-color);
    transition: color 0.3s;
}
.sports__details p {
    font-size: 12px;
    color: var(--hover-color);
    transition: color 0.3s;
}

.sports__details span {
    font-size: 10px;
    color: var(--text-color);
}

/* Light & Dark Mode */
:root {
    --background-color: #f9f9f9;
    --text-color: #222;
    --gradient: linear-gradient(135deg, #ff9a9e, #fad0c4);
    --hover-color: #866301;
    --sparkle-color: rgba(126, 90, 1, 0.863);
}

[data-theme='dark'] {
    --background-color: #121826;
    --text-color: #ffffff;
    --gradient: linear-gradient(135deg, #972b3d, #934864);
    --hover-color: #ff6b6b;
    --sparkle-color: rgba(240, 115, 70, 0.7);
}
